<template lang="html">
  <div class="page" ref="popWrapper">
    <div class="content">
<a name="process108"></a>

<p><br></p>
<p><span class="rvts17">章节探索：结识同伴，进入欢乐堡</span></p>
<p class="rvps2"><span class="rvts41">起始坐标：</span>
<br><span class="rvts20">X:184,Y:366</span><span class="rvts21">，地点：默认初始点</span>
<br><span class="rvts23"></span>
<br><span class="rvts41">目标：</span>
<br><span class="rvts21">结识同伴，进入欢乐堡，并大致了解这里的情况。</span>
<br><span class="rvts23"></span>
<br><span class="rvts41">详情：</span>
<br><img height="auto" alt="" hspace="1" vspace="1" src="../../../assets/pic2/abcamber1001.jpg" display="block" max-width="100%"><span class="rvts23"></span>
<br><span class="rvts21">在经历了沉船事故后，角色被冲到岸边，这里离欢乐堡并不遥远。刚开始，需要熟悉各种操作以及适应游戏的世界观。</span>
<br><span class="rvts21">首先在离我们初始地点东边台阶处，就是一尊石头雕像，石头雕像可以进行祈祷，并进行传送。这点和大多数</span><span class="rvts20">RPG</span><span class="rvts21">游戏是相同的。</span>
<br><span class="rvts21">不同的是，可以随时随地使用传送（按下</span><span class="rvts20">ESC</span><span class="rvts21">菜单，进行路线传送），而无需到达雕像传送点处进行操作。</span>
<br><span class="rvts23"></span>
<br><img height="auto" alt="" hspace="1" vspace="1" src="../../../assets/pic2/abcamber1002.jpg" display="block" max-width="100%"><span class="rvts23"></span>
<br><span class="rvts21">欢乐堡大门位于地图的</span><span class="rvts20">X:203,Y:206</span><span class="rvts21">位置。刚到这里，就会遭遇卢卡斯大王与手下达利斯的一出君臣戏。</span>
<br><span class="rvts21">他们最终一个唱黑脸一个唱白脸，处死了可怜的蜥蜴。之后，便可以进入欢乐堡。在进入欢乐堡之前，最好先召集以下</span><span class="rvts20">6</span><span class="rvts21">位随行的伙伴，不然的话，很容易被里面的帮派给群殴。</span>
<br><span class="rvts21">说到这个剧情，如果已经很熟悉原罪</span><span class="rvts20">2</span><span class="rvts21">这款游戏，并有一些奇妙的想法，比如说尝试攻打达利斯（女大锤），这是完全有可能的。首先，需要在不触发这段剧情的情况下，先获得</span><span class="rvts20"></span><span class="rvts21">传送手套</span><span class="rvts20"></span><span class="rvts21">，该物品是在任务</span><span class="rvts20"></span><span class="rvts21">传送装置</span><span class="rvts20"></span><span class="rvts21">中，一支大鳄鱼掉落的。之后，当角色和队伍其它成员达到一定的等级，可以尝试在高墙上进行埋伏。可以把她传送到高墙进行战斗，也可以从高处朝她脸上扔一个重量级的木桶（最好把所有沉重的物品都放进去），这样可以造成很大的伤害。当达利斯生命值低于一定数值后，她会变成一支龙飞走。这样做，并不会影响以后的剧情。</span>
<br><span class="rvts23"></span>
<br><img height="auto" alt="" hspace="1" vspace="1" src="../../../assets/pic2/abcamber1003.jpg" display="block" max-width="100%"><span class="rvts23"></span>
<br><span class="rvts21">从初始地点出发，只要沿着西海岸一直走，就会首先遇到腥红王子（</span><span class="rvts20">The Red King</span><span class="rvts21">）在这里晒太阳，看大海，与他交谈，他变会加入你的队伍。</span>
<br><span class="rvts21">腥红王子的初始坐标为</span><span class="rvts20">X:167,Y:281</span><span class="rvts21">。</span>
<br><span class="rvts21">需要知道，所有的可以加入队伍的</span><span class="rvts20">6</span><span class="rvts21">名队友，都是种族篇中的源力者，他们每个人都有自己的单独的一条任务线（详见全伙伴任务篇）。可以拥有最多</span><span class="rvts20">3</span><span class="rvts21">个随从伙伴，并且在与他们第一次对话中设定他们的职业类型。之后，将无法改变他们的职业。腥红王子的默认职业是战士，持单手武器和盾牌，主修土元素和战争学派，主要属性是力量（</span><span class="rvts20">Strength</span><span class="rvts21">）和体质（</span><span class="rvts20">Constitution</span><span class="rvts21">）。使用他最大的好处是，他有免铁锹的挖图寻宝技能（其实用途不大），并且拥有种族特有的</span><span class="rvts20">AOE</span><span class="rvts21">喷火技能。</span>
<br><span class="rvts23"></span>
<br><img height="auto" alt="" hspace="1" vspace="1" src="../../../assets/pic2/abcamber1004.jpg" display="block" max-width="100%"><span class="rvts23"></span>
<br><span class="rvts21">在进入欢乐堡之前，如果一直朝东探索，经历一个隐蔽的山洞，变便会找到费恩（</span><span class="rvts20">Fane</span><span class="rvts21">）。发现他的时候，他正在考虑如何变形。</span>
<br><span class="rvts21">初次遇到他在这里的坐标是</span><span class="rvts20">X:271,Y:275</span><span class="rvts21">。</span>
<br><span class="rvts21">说到这里不得不提费恩，他其实并不是个真正的不死族，他的种族是一种奇特的，可以进行基因变换的种族（之后第二章会讲到）。他可以变成人族，也可以变成精灵，蜥蜴等</span><span class="rvts20">... </span><span class="rvts21">并且当它变形后，他可以继承所有变形后种族的原始天赋。费恩默认的职业是巫师，使用双魔杖，或单魔杖</span><span class="rvts20">+</span><span class="rvts21">盾牌。主修火元素和土元素，主要属性是智力（</span><span class="rvts20">Intelligence</span><span class="rvts21">）。费恩是不死族，因此可以从毒元素中获得治疗。这使得他免疫有毒的地表和云雾，并可以吃下有毒物品进行生命恢复。相反的是，费恩在接受正常治疗技能时，会收到伤害。</span>
<br><span class="rvts23"></span>
<br><img height="auto" alt="" hspace="1" vspace="1" src="../../../assets/pic2/abcamber1005.jpg" display="block" max-width="100%"><span class="rvts23"></span>
<br><span class="rvts21">遇到的第</span><span class="rvts20">3</span><span class="rvts21">个伙伴，就是刚刚进入欢乐堡要塞，当百姓调节员的伊凡</span><span class="rvts20"></span><span class="rvts21">本美兹（</span><span class="rvts20">Ifan Ben-Mezed</span><span class="rvts21">）。</span>
<br><span class="rvts21">他的初始坐标为</span><span class="rvts20">X:207,Y:181</span><span class="rvts21">。</span>
<br><span class="rvts21">想要让他加入队伍，必须首先完成</span><span class="rvts20"></span><span class="rvts21">彻底搜查（</span><span class="rvts20">The Shakedown</span><span class="rvts21">）</span><span class="rvts20"></span><span class="rvts21">的初始部分。伊凡是军人出身，拥有比较老道磁性的</span><span class="rvts20"></span><span class="rvts21">混社会</span><span class="rvts20"></span><span class="rvts21">人群的口音和对话。因此，这使得他在很多场合的对话会显得比较粗鲁和干练。伊凡默认的职业是旅人，使用弓和弩，他主修猎人和土元素，默认主属性为灵巧（</span><span class="rvts20">Finess</span><span class="rvts21">）。伊凡默认自带</span><span class="rvts20"></span><span class="rvts21">动物伙伴（</span><span class="rvts20">Pet Pal</span><span class="rvts21">）</span><span class="rvts20"></span><span class="rvts21">天赋，可以和动物谈话，而引出额外剧情。</span>
<br><span class="rvts23"></span>
<br><img height="auto" alt="" hspace="1" vspace="1" src="../../../assets/pic2/abcamber1006.jpg" display="block" max-width="100%"><span class="rvts23"></span>
<br><span class="rvts21">距离上节讲到的伊凡</span><span class="rvts20"></span><span class="rvts21">本美兹不远处，就是洛许（</span><span class="rvts20">Lohse</span><span class="rvts21">），洛许是一个似乎被恶魔时不时控制心灵的精神病患者。想要洛许加入队伍十分简单，因为洛许比较喜欢冒险，而且还比较健谈，属于典型的破产姐妹类型。</span>
<br><span class="rvts21">她的初始位置是</span><span class="rvts20">X:206,Y:169</span><span class="rvts21">。</span>
<br><span class="rvts21">然而需要注意，洛许旁边有一个卢卡斯的崇拜者，如果你一旦和他发生打斗，洛许会帮助这个崇拜者。因此再与她对话之前，尽可能的不要招惹她旁边的这个神经质（不要试图拿走或偷走卢卡斯雕像周围的贡品或与崇拜者发生争执）。洛许默认职业为附魔师，使用法杖效果最佳。洛许双修气元素和水元素，主属性为智力（</span><span class="rvts20">Intelligence</span><span class="rvts21">）。</span><span class="rvts20"></span><span class="rvts21">洛许的作用不单可以作为</span><span class="rvts20">AOE</span><span class="rvts21">输出，还可以作为辅助或团队的</span><span class="rvts20">DEBUFF</span><span class="rvts21">清理者（因为水元素派系更偏向于治愈和解控）。</span>
<br><span class="rvts23"></span>
<br><img height="auto" alt="" hspace="1" vspace="1" src="../../../assets/pic2/abcamber1007.jpg" display="block" max-width="100%"><span class="rvts23"></span>
<br><span class="rvts21">在欢乐堡的边缘地带，会遇到一个鬼鬼祟祟的精灵女刺客，她就是塞碧莉（</span><span class="rvts20">Sebille</span><span class="rvts21">）。塞碧莉的初始位置位于欢乐堡中心位置的厨房口处（在被锁着的精灵旁边门口的一个木桶后）。然而，在经历了</span><span class="rvts20"></span><span class="rvts21">被困精灵（</span><span class="rvts20">The Imprisoned Elf</span><span class="rvts21">）</span><span class="rvts20"></span><span class="rvts21">任务的开场后，塞碧莉会尾随一个蜥蜴商人斯汀泰尔（</span><span class="rvts20">Stingtail</span><span class="rvts21">）并时刻观察着他。</span>
<br><span class="rvts21">她的最终位置来到了这里，坐标为</span><span class="rvts20">X:183,Y:101</span><span class="rvts21">。</span>
<br><span class="rvts21">塞碧莉是个出名的刺客，性格冷酷，行动果断。可以招募她并使用她默认的刺客职业进行近战输出。塞碧莉默认单修恶棍系技能，主属性为灵巧（</span><span class="rvts20">Finess</span><span class="rvts21">）。</span>
<br><span class="rvts23"></span>
<br><img height="auto" alt="" hspace="1" vspace="1" src="../../../assets/pic2/abcamber1008.jpg" display="block" max-width="100%"><span class="rvts23"></span>
<br><span class="rvts21">矮人野兽（</span><span class="rvts20">Beast</span><span class="rvts21">）的位置一般是最后被发现在欢乐堡外围海岸处，正在修理船只。</span>
<br><span class="rvts21">坐标为</span><span class="rvts20">X:124,Y:163</span><span class="rvts21">。</span>
<br><span class="rvts21">野兽的默认职业为战法，使用双手近战武器，配合气元素的控制技能，进行近战输出。他的默认派系为气元素和战争派系，主属性为力量和智力。属于输出和控制都较为平庸的职业。野兽拥有矮人族的石化之触，因此是一个比较难被控制的角色，配合战法职业优势，可以很好体现在战场上的增援和机动效果方面。</span></p>
<p><br></p>
<p><br></p>

    </div>
  </div>
</template>
<script>
import BScroll from 'better-scroll'
export default {
  data () {
    return {};
  },
  methods: {
    _initScroll() {
      this.popScroll = new BScroll(this.$refs.popWrapper, {
        click: true,
        probeType: 3
      });
    }
  },
  created() {
    this.$nextTick(() => {
      this._initScroll();
    });
  }
};
</script>
<style>
@import '../../../../static/css/common.css';
</style>



